

const defaultQuotes = [
    'Run it up the flagpole',
    'Its as useless as a ashtray on a motorcycle',
    'Turn the handle',
    'Its a doddle',
    'Just farting around',
    'About as helpful as a chocolate fire-guard',
    'throw it at the wall, see what sticks',
]


let customQuotes;
try {
    let params = (new URL(document.location)).searchParams;
    let encQuotes = params.get('ss_quotes'); 
    if(encQuotes) {
        customQuotes = atob(encQuotes).split('\n').map(x => x.trim());
    }
} catch(err) {
    // nothing to see here
}

export const quotes = customQuotes?.length > 0 ? customQuotes : quotes;