import { Howl } from "howler";
import { audio1Src, audio2Src, audio3Src, audio4Src, gameOverSrc } from './audio';

const userIdKey = 'simon-says-userid'
if (window.localStorage.getItem(userIdKey) == null) {
    const id = Number(Math.random().toString().slice(2)).toString(16) + Number(Math.random().toString().slice(2)).toString(16) + Number(Math.random().toString().slice(2)).toString(16);
    window.localStorage.setItem(userIdKey, id)
}
export function sleep(ms) {
    return new Promise((resolve, reject) => {
        setTimeout(resolve, ms);
    })
}
export function giveMeSomeMoves(count) {
  const moves = [];
  while (true) {
    var newMoves = Math.random()
      .toString()
      .slice(2)
      .split("")
      .map((x) => (x % 4) + 1);
    for (const m of newMoves) {
      moves.push(m);
      if (moves.length === count) return moves;
    }
  }
}

export function timer(timeout, callback) {
    let interval;
    function tick () {
        if(interval) {
            clearTimeout(interval);
        }
        interval = setTimeout(() => {
            callback() 
         }, timeout);
     
    }

    function stop() {
        if(interval) {
            clearInterval(interval);
            interval = null
        }
    }

    function reset(ms) {
        timeout = ms || timeout
        tick();
    }
    return {reset, stop}
}

var ua = window.navigator.userAgent;
const iOS = !!ua.match(/iPad/i) || !!ua.match(/iPhone/i);
const webkit = !!ua.match(/WebKit/i);
const iOSSafari = iOS && webkit && !ua.match(/CriOS/i);

export const browserInfo = {iOS, webkit, iOSSafari};

export const  gameOverSound = new Howl({
    src: [gameOverSrc],
    html5: iOS,
    format: 'mp3'

  });

var sound1 = new Howl({
  src: [audio1Src],
  html5: iOS,
  format: 'mp3'
});

var sound2 = new Howl({
  src: [audio2Src],
  html5: iOS,
  format: 'mp3'
});

var sound3 = new Howl({
  src: [audio3Src],
  html5: iOS,
  format: 'mp3'
});

var sound4 = new Howl({
  src: [audio4Src],
  html5: iOS,
  format: 'mp3'
});

function play(sound) {
  const p = new Promise((resolve, reject) => {
    sound.once("end", resolve);
    sound.play();
  });

  return p;
}

export function playAudio(position) {
  switch (position) {
    case 1: {
      return play(sound1);
    }
    case 2: {
      return play(sound2);
    }
    case 3: {
      return play(sound3);
    }
    case 4: {
      return play(sound4);
    }
  }
}

